.app__navbar {
  display: flex;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.45);
  backdrop-filter: blur(4px);
  position: fixed;
  z-index: 2;
  width: 100%;
}

.app__navbar-img {
  width: 185px;
}

.app__navlink {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  color: var(--gray-color);
}

.app__navlink li {
  padding-left: 35px;
  padding-right: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 5px;
}
.app__navlink li:hover a {
  color: var(--secondary-color);
}
.app__navlink li:hover div {
  background-color: var(--secondary-color);
}

.app__navlink li a {
  text-decoration: none;
  color: var(--gray-color);
  text-transform: uppercase;
  font-weight: 500;
  transition: all 0.3s ease-in-out;
}

.app__navlink li div {
  margin-bottom: 5px;
  width: 8px;
  height: 8px;
  background: transparent;
  border-radius: 100%;
}

.app__navlink li a:hover {
  color: var(--secondary-color);
}

.app__navlink li div:hover {
  background-color: var(--secondary-color);
}

@media screen and (max-width: 900px) {
  .app__navlink {
    display: none;
  }
}
.app__navbar-menu {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-color);
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 20px;
}

.app__navbar-menu svg {
  width: 70%;
  height: 70%;
  color: var(--white-color);
}

.app__navbar-menu div {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 5;
  padding: 1rem;
  width: 80%;
  height: 100vh;
  background: url("../../assets/bgWhite.png");
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  /* background: url('../../assets/bgWhite.png'); */
  background-color: var(--white-color);
  background-size: cover;
  background-repeat: repeat;
  /* top box shadow */
  box-shadow: 0px 0px 20px rgba(168, 168, 168, 0.15);
}

.app__navbar-menu div svg {
  width: 35px;
  height: 35px;
  color: var(--secondary-color);
  margin: 0.5rem 1rem;
}

.app__navbar-menu div ul {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.app__navbar-menu div ul li {
  margin: 1rem;
}

.app__navbar-menu div ul li a {
  color: var(--gray-color);
  text-decoration: none;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
  transition: all 0.3s ease-in-out;
}

@media screen and (min-width: 900px) {
  .app__navbar-menu {
    display: none;
  }
}

